.container {
  color: #fff;
  background-color: #1c2237;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.content-container {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  padding: 20px;
  align-items: center;
}

.image-container {
  height: 80%;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.text-wrapper {
  padding-left: 50px;
}

.top-line {
  color: #f00946;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.heading {
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: #f7f8fa;
}

.dark {
  color: #1c2237;
}

.subtitle {
  font-size: 18px;
}

.button {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

@media screen and (max-width: 960px) {
  .image-container {
    height: auto;
    width: 200px;
  }

  .heading {
    text-align: center;
  }

  .top-line {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 5px;
  }

  .content-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .text-wrapper {
    padding: 0px;
    margin-top: 20px;
    height: auto;
  }

  .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .heading {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 500;
  }

  .button {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .subtitle {
    text-align: center;
  }
}
